@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first
@mixin vp-1299 {
  @media (max-width: ($vp-1300 - 1px)) {
    @content;
  }
}

@mixin vp-1249 {
  @media (max-width: ($vp-1250 - 1px)) {
    @content;
  }
}

@mixin vp-1199 {
  @media (max-width: ($vp-1200 - 1px)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1px)) {
    @content;
  }
}

@mixin vp-desktop-only {
  @media (min-width: $vp-1200) {
    @content;
  }
}

@mixin vp-1199-retina {
  @media (max-width: ($vp-1200 - 1px)) and (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin vp-767-retina {
  @media (max-width: ($vp-768 - 1px)) and (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin desktop-retina {
  @media (min-width: ($vp-1200)) and (min-resolution: $retina-dppx) {
    @content;
  }
}
